import React, { Component } from 'react';
import { Col, Card, CardBody } from "reactstrap";

import { Badge } from "reactstrap"
import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import 'react-bootstrap-table2-paginator/dist/react-bootstrap-table2-paginator.min.css';
import '../Jobs/custom.css'

class LatestTransactions extends Component {
  constructor(props) {
    super(props);
    this.state = {
      menu: false,
    }
  }

  render() {
    const JobsColumns = [
      {
        dataField: "name",
        text: "Name",
        sort: true,
      },
      {
        dataField: "labour_rate",
        text: "Labor Rate",
        sort: true,
        formatter: (cellContent, row) => (
          row.labour_rate
        ),
      },
      {
        dataField: "desired_gross_margin",
        text: "Desired Gross Margin",
        sort: true,
      },
      {
        dataField: "actual_gross_margin",
        text: "Actual Gross Margin",
        sort: true,
      },
      {
        dataField: "target_selling_price",
        text: "Target Selling Price",
        sort: true,
      },
      {
        dataField: "actual_selling_price",
        text: "Actual Selling Price",
        sort: true,
      },
      {
        dataField: "status",
        text: "Status",
        sort: true,
        formatter: (cellContent, row) => (
          row.status ==="todo"?(
            <Badge
            className={"font-size-12 badge-soft-success"}
            color={"success"}
            pill
          >
            {row.status}
          </Badge>
            ):(
              row.status ==="pending"?(
                <Badge
                  className={"font-size-12 badge-soft-warning"}
                  color={"warning"}
                  pill
                >
                  {row.status}
                </Badge>
                
                ):(
                  <Badge
                  className={"font-size-12 badge-soft-danger"}
                  color={"danger"}
                  pill
                >
                  {row.status}
                </Badge>
                  )
              )
        ),
      },
    ]
    const data = {
      columns: [
        {
          dataField: 'name',
          text: 'Name'
        },
        {
          dataField: "orderId",
          text: "Order ID"
        },
        {
          dataField: "date",
          text: "Date"
        },
        {
          dataField: "billingName",
          text: "Billing Name"
        },
        {
          dataField: "total",
          text: "Total"
        },
        {
          dataField: "status",
          text: "Payment Status"
        },
      ],
      
    };

    const options = {
      // pageStartIndex: 0,
      hideSizePerPage: false,
      hidePageListOnlyOnePage: false,
      sizePerPageList:
        [{
          text: '5th', value: 5
        }, {
          text: '10th', value: 10
        }, {
          text: 'All', value: this.props.jobs.length
        }]
    };

    return (
      <React.Fragment>
        <Col lg={12}>
          <Card>
            <CardBody>
              

              <h4 className="card-title mb-4">Jobs</h4>
              <div className="table-responsive dashboard">

              <BootstrapTable
                keyField='id'
                data={this.props.jobs}
                columns={JobsColumns}
    
                pagination={paginationFactory(options)}
  
              />
              </div>

            </CardBody>
          </Card>
        </Col>
      </React.Fragment>
    );
  }
}

export default LatestTransactions;