import React, { Component } from 'react';
import { Card, CardBody, Row, Col } from "reactstrap";
import { BaseUrl } from "../../helpers/Baseurl"

//Import Charts
import ReactApexChart from 'react-apexcharts';
import "./dashboard.scss";

class SalesAnalytics extends Component {
    constructor(props) {
        super(props);
        this.state = {
            series: [1,1, 1],
            options: {
                labels: ["Todo", "Pending", "Completed"],
                plotOptions: {
                    pie: {
                        donut: {
                            size: '70%'
                        }
                    }
                },
                dataLabels: {
                    enabled: false
                },
                legend: {
                    show: false,
                },
                colors: ['#1cbb8c', '#eeb902', '#ff3d60'],

            }
        }
    }   

    componentDidCatch(error) {
        // Changing the state to true
        // if some error occurs
        console.log(error)
      }
    async componentDidMount() {
 
        const requestOptions = {
               method: 'GET',
               headers: { 'Content-Type': 'application/json' },
           };
           await fetch(`${BaseUrl}/api/jobs/jobs/`,requestOptions)
               .then(res => res.json())
               .then(
                   (data) => {

                       let data_length = data.length
                       let completed = 0
                       let pending = 0
                       let todo = 0
                       for (let i=0;i<data_length; i++ ){
                           if(data[i].status === "todo"){
                            todo = todo+1 
                           }else if (data[i].status === "pending"){
                            pending = pending +1 
                           }else {
                            completed = completed+1
                           }
                       }
                        let todo_per = (todo *100)/data_length
                        let pending_per = (pending *100)/data_length
                        let completed_per = (completed *100)/data_length
                        this.setState({series: [todo_per, pending_per, completed_per]})
                   },
                   (error) => {
                       console.log(error, 'error--------------') 
                   }
               )  
     }


    render() {
    
        return (
            <React.Fragment>
                <Card>
                    <CardBody>
                       
                        <h4 className="card-title mb-4">Jobs Analytics</h4>

                        <div id="donut-chart" className="apex-charts">
                            <ReactApexChart options={this.state.options} series={this.state.series} type="donut" height="250" />
                        </div>

                        <Row>
                            <Col xs={4}>
                                <div className="text-center mt-4">
                                    <p className="mb-2 text-truncate"><i className="mdi mdi-circle text-success font-size-10 me-1"></i> Todo</p>
                                    <h5>{this.state.series[0]} %</h5>
                                </div>
                            </Col>
                            <Col xs={4}>
                                <div className="text-center mt-4">
                                    <p className="mb-2 text-truncate"><i className="mdi mdi-circle text-warning font-size-10 me-1"></i> Pending</p>
                                    <h5>{this.state.series[1]} %</h5>
                                </div>
                            </Col>
                            <Col xs={4}>
                                <div className="text-center mt-4">
                                    <p className="mb-2 text-truncate"><i className="mdi mdi-circle text-danger font-size-10 me-1"></i> Completed</p>
                                    <h5>{this.state.series[2]} %</h5>
                                </div>
                            </Col>
                        </Row>
                    </CardBody>
                </Card>
            </React.Fragment>
        );
    }
}

export default SalesAnalytics;