import React, { Component } from "react";
import { Container, Row, Col } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from '../../components/Common/Breadcrumb';
import { BaseUrl } from "../../helpers/Baseurl"

//Import Components
import MiniWidgets from "./MiniWidgets";
import RevenueAnalytics from "./RevenueAnalytics";
import SalesAnalytics from "./SalesAnalytics";
import LatestTransactions from "./LatestTransactions";

class Dashboard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            jobs : [],
            breadcrumbItems : [],
            reports : []
        }
    }
    componentDidCatch(error) {
        // Changing the state to true
        // if some error occurs
        console.log(error)
      }
    async componentDidMount() {
 
        const requestOptions = {
               method: 'GET',
               headers: { 'Content-Type': 'application/json' },
           };
           await fetch(`${BaseUrl}/api/jobs/jobs/`,requestOptions)
               .then(res => res.json())
               .then(
                   (data) => {

                       let data_length = data.length
                       let completed = 0
                       let pending = 0
                       let todo = 0
                       for (let i=0;i<data_length; i++ ){
                           if(data[i].status === "todo"){
                            todo = todo+1 
                           }else if (data[i].status === "pending"){
                            pending = pending +1 
                           }else {
                            completed = completed+1
                           }
                       }
                       let reports = [{ icon : "ri-stack-line", title : "Number of Todo Jobs", value : todo , rate : data_length, desc : "From previous period" },
                        { icon : "ri-store-2-line", title : "Number of Pending Jobs", value : pending, rate : data_length, desc : "From previous period" },
                        { icon : "ri-briefcase-4-line", title : "Number of Completed Jobs", value : completed, rate : data_length, desc : "From previous period" },
                        ]
                        let todo_per = (todo *100)/data_length
                        let pending_per = (pending *100)/data_length
                        let completed_per = (completed *100)/data_length
                   this.setState({  jobs: data,
                                    reports:reports,
                                    completed :  parseInt(completed_per),
                                    pending :  parseInt(pending_per),
                                    todo : parseInt(todo_per),

                                          })
                   },
                   (error) => {
                       console.log(error, 'error--------------') 
                   }
               )  
     }

    render() {
        const {jobs,reports} = this.state
        return (
            <React.Fragment>
                <div className="page-content">
                    <Container fluid>

                    <Breadcrumbs title="Dashboard" breadcrumbItems={[]} />
                        <Row>
                            <Col xl={12}>
                                <Row>
                                    <MiniWidgets reports={reports} />
                                </Row>
                            </Col>
                            <Col xl={8}>
                            <RevenueAnalytics/>
                            </Col>
                            <Col xl={4}>
                                {/* sales Analytics */}
                                <SalesAnalytics
                                 />
                            </Col>
                        </Row>
                        <Row>
                        
                            {/* latest transactions */}
                            <LatestTransactions
                            jobs = {jobs}/>
                        </Row>

                    </Container> 
                </div>
            </React.Fragment>
        );
    }
}

export default Dashboard;
